@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400..700&family=Raleway:ital,wght@0,100..900;1,100..900&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

html, body {
  height: 100%;  /* Ensure full height */
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;  /* Full viewport height */
}

section {
  margin-top: 0.5rem;  /* Reduced margin-top for sections */
  margin-bottom: 1rem; /* Reduced margin-bottom for sections */
  padding: 2rem 1rem; /* Padding to prevent content from touching edges */
}

.container {
  padding-left: 1rem; /* Adjust horizontal padding */
  padding-right: 1rem;
  max-width: 100%;  /* Ensure full width on small screens */
}

h1, h2, h3, h4 {
  margin-bottom: 0.5rem;  /* Reduced margin-bottom for headings */
  @apply text-4xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-orange-400 to-pink-600 inline-block;
  font-family: 'Dancing Script', cursive;
}

button, a {
  margin: 0.25rem;  /* Reduced margin for buttons and links */
}

* {
  box-sizing: border-box;
}

.my-custom-class {
  @apply text-pink-800;
}

.bg-primary-color {
  @apply px-1 py-2 w-full rounded-xl mr-4 bg-gradient-to-r from-orange-500 to-pink-500 font-bold hover:border-none;
}

#Contact {
  padding-top: 0.25rem; /* Reduced padding-top for Contact section */
}

/* Ensure that sections do not overlap in mobile view */
section {
  padding: 2rem 1rem;  /* Added padding to sections */
}

@media (min-width: 768px) {
  .section-gap {
    margin-top: 0.25rem;  /* Reduced margin-top for larger screens */
  }
  .container {
    padding-left: 2rem;
    padding-right: 2rem; /* Larger padding on bigger screens */
  }
}

@media (max-width: 768px) {
  .container {
    padding-left: 1rem;
    padding-right: 1rem;  /* Reduced padding on smaller screens */
  }
}
